import { Grid, makeStyles, CardContent } from '@material-ui/core';
import { gql, useMutation, useQuery } from '@apollo/client';
import _get from 'lodash.get';
import { useEffect, useState } from 'react';
import { gridSpacing } from '../../../store/constant';
import MainCard from '../../../ui-component/cards/MainCard';
import Status from './components/Status';
import graphqlSchema from '../../../graphql';
import { useSnackBar } from '../../../hooks';
import StatusConfirmationDialog from './components/StatusConfirmationDialog';
import { STATUS_CONFIRMATION_MESSAGES } from './config';
import ReviewSellerSubmission from './components/ReviewSellerSubmission';
import Loader from '../../../ui-component/Loader';
import { reorderCategories } from '../../../helpers';

const useStyles = makeStyles({
	mainCard: {
		border: 'none',
	},
});

const PropertyActions = ({ data, refetch }: any) => {
	const classes = useStyles();
	const showSnackBar = useSnackBar();
	const [status, setStatus] = useState(0);
	const [open, setOpen] = useState(false);

	const { data: listFormCategories, loading: isLoadingFormCategories } =
		useQuery(gql(graphqlSchema.listFormCategories));

	const property = _get(data, 'getProperty');
	const formStatuses = _get(property, 'PropertyFormDataStatuses') || [];
	const propertyId = _get(property, 'PropertyId');
	const formCategories = _get(listFormCategories, 'listFormCategories') || [];
	const formCategoriesSorted = reorderCategories(formCategories);

	const [updatePropertyStatus, { loading: isUpdatePropertyStatus }] =
		useMutation(gql(graphqlSchema.updatePropertyStatus), {
			onCompleted: () => {
				showSnackBar({
					message: 'Property status has updated',
					alertSeverity: 'success',
				});
			},
			onError: e => {
				const message = _get(e, 'message', 'Something went wrong');
				showSnackBar({ message });
			},
		});

	useEffect(() => {
		const currentStatus = _get(property, 'StatusId', 0);
		setStatus(currentStatus);
	}, [property]);

	const message = String(_get(STATUS_CONFIRMATION_MESSAGES, status + 1, ''));

	const updateStatus = (status: number) => {
		updatePropertyStatus({
			variables: {
				PropertyId: propertyId,
				StatusId: status,
			},
		});
		setStatus(status);
	};

	const onOpenConfirmationDialog = () => {
		setOpen(true);
	};

	const onClose = () => {
		setOpen(false);
	};

	const onConfirm = () => {
		const nextStatus = status + 1;
		updateStatus(nextStatus);
		setOpen(false);
	};

	return (
		<MainCard content={false} className={classes.mainCard}>
			{isLoadingFormCategories && <Loader />}
			<CardContent>
				<Grid container spacing={gridSpacing} alignItems='flex-start'>
					<Grid item lg={4} xs={12}>
						<Status
							property={property}
							isUpdatePropertyStatus={isUpdatePropertyStatus}
							status={status}
							onNextClick={onOpenConfirmationDialog}
							onBackClick={updateStatus}
						/>
					</Grid>
					<Grid item lg={8} xs={12}>
						{!isLoadingFormCategories && (
							<ReviewSellerSubmission
								propertyId={propertyId}
								formStatuses={formStatuses}
								formCategories={formCategoriesSorted}
								refetch={refetch}
							/>
						)}
					</Grid>
				</Grid>
			</CardContent>
			<StatusConfirmationDialog
				open={open}
				message={message}
				onClose={onClose}
				onConfirm={onConfirm}
			/>
		</MainCard>
	);
};

export default PropertyActions;
