import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { gql } from '@apollo/client';
import {
	TEditDoc,
	TDocSetting,
	TMappedSettings,
	TDocTypeSetting,
	TMappedDocTypes,
	TPropertyDoc,
	TSearchFilters,
} from './types';
import graphqlSchema from '../../../../../graphql';

const REMOVE_DOC_EVENT_NAME = 'REMOVE';
const UNCATEGORIZED_DOC_TYPE = 'Uncategorized';
export const PROPERTY_SCOPE = 'Property';

const initialDoctype: TDocTypeSetting = {
	id: '',
	name: '',
	scope: '',
	availablePermissions: [],
};

export const initialDoc: TEditDoc = {
	objectKey: '',
	propertyId: '',
	offerId: '',
	offerNo: '',
	docType: initialDoctype,
	docPermissions: [],
	docStatus: '',
	files: [],
};

export const mapSimpleSettings = (
	settings: Array<TDocSetting>
): TMappedSettings =>
	settings.reduce(
		(result, setting) => ({ ...result, [setting.id]: setting.name }),
		{}
	);

export const mapDocTypes = (
	docTypes: Array<TDocTypeSetting>
): TMappedDocTypes =>
	docTypes.reduce(
		(result, type) =>
			type.id === UNCATEGORIZED_DOC_TYPE
				? result
				: {
						...result,
						[type.id]: {
							availablePermissions: _get(type, 'available_permissions'),
							scope: type.scope,
							id: type.id,
							name: type.name,
						},
				  },
		{}
	);

export const doesNeedOffer = (docType: TDocTypeSetting) =>
	![PROPERTY_SCOPE, ''].includes(_get(docType, 'scope'));

export const sortDocuments = (docs: TPropertyDoc[]) =>
	docs.slice().sort((doc1, doc2) => {
		const time2 = new Date(doc2.EventTime).getTime();
		const time1 = new Date(doc1.EventTime).getTime();
		return time2 - time1;
	});

const getFilterValues = (filters: TDocSetting[] | TDocTypeSetting[]) =>
	filters.map(filter => filter.id);

const isInvalidSearchFilter = (values: TSearchFilters) =>
	Object.keys(values).every(key => _isEmpty(_get(values, key)));

const hasText = (doc: TPropertyDoc, text: string) => {
	if (!text) return true;

	const docName = _get(doc, 'File.name', '');
	return docName.toLowerCase().includes(text.toLowerCase());
};

const hasTags = ({
	doc,
	filterTags,
	key,
	isSingleValue = true,
}: {
	doc: TPropertyDoc;
	filterTags: TDocTypeSetting[] | TDocSetting[];
	key: string;
	isSingleValue?: boolean;
}) => {
	if (_isEmpty(filterTags)) return true;

	const filterValues = getFilterValues(filterTags);
	const docTagValue = _get(doc, [key], isSingleValue ? '' : []);
	return isSingleValue
		? filterValues.includes(docTagValue)
		: docTagValue.some((val: string) => filterValues.includes(val));
};

export const searchDocuments = (
	docs: TPropertyDoc[],
	values: TSearchFilters
) => {
	if (isInvalidSearchFilter(values)) return docs;

	const { search, docTypes, docStatuses, docPermissions } = values;

	return docs.filter(
		doc =>
			hasText(doc, search) &&
			hasTags({ doc, key: 'DocType', filterTags: docTypes }) &&
			hasTags({ doc, key: 'DocStatus', filterTags: docStatuses }) &&
			hasTags({
				doc,
				key: 'DocPermissions',
				filterTags: docPermissions,
				isSingleValue: false,
			})
	);
};

export const updateDocsCache = (propertyId: string) => ({
	document: gql(graphqlSchema.onUpdateDocItem),
	variables: { PropertyId: propertyId },
	updateQuery: (
		prev: Object,
		{ subscriptionData }: { subscriptionData: Object }
	) => {
		if (!_get(subscriptionData, 'data')) return prev;

		const existingProperty = _get(prev, 'getProperty');
		const docs: TPropertyDoc[] = _get(existingProperty, 'Docs');
		const updatedDoc = _get(subscriptionData, 'data.docs_onUpdateDocItem', {});
		const eventName = _get(updatedDoc, 'EventName', {});
		const updatedDocFileId = _get(updatedDoc, 'FileId');
		const filteredDocs = docs.filter(item => item.FileId !== updatedDocFileId);

		return {
			getProperty: {
				...existingProperty,
				Docs:
					eventName === REMOVE_DOC_EVENT_NAME
						? filteredDocs
						: [...filteredDocs, updatedDoc],
			},
		};
	},
});
